/* Watermark */
.watermark {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('./img/Watermark.png');
    background-repeat: repeat;
    background-size: auto 100%;
    background-position: center center;
    opacity: 0.4;
    pointer-events: none;
    z-index: 5000;
}

/* Splash Screen */
.splash {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #242424;
    z-index: 200;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.logo1 {
    position: relative;
    top: -100;
    z-index: 5;
    width: 40vh;
    height: auto;
    opacity: 0;
    transition: transform 4s linear, opacity 2s ease-in-out;
}

.logo2 {
