    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    width: 40vh;
    max-width: 300px;
    height: auto;
    opacity: 1;
}

.fade-in {
    opacity: 0;
    animation: fadeIn 1s ease-in forwards;
}

.splash.display-none {
    opacity: 0;
    z-index: -100;
    transition: opacity 1s ease-in-out, z-index 0s 1s; /* Delay z-index change */
}


