/* bgmVolume */

#bgmVolume {
    -webkit-appearance: none;
    appearance: none;
    background: transparent;
    outline: none;
    border: none;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    pointer-events: all;
    cursor: pointer;
    z-index: 10;
}

#bgmVolume::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    height: 5px;
    background: transparent;
    border: none;
    position: relative;
}

#bgmVolume::before {
    -webkit-appearance: none;
    appearance: none;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: #e4d7c7;
    z-index: -1;
    clip-path: polygon(
        3px 0%,
        calc(100% - 3px) 0%,
        100% 50%,
        calc(100% - 3px) 100%,
        3px 100%,
        0% 50%);
}

#bgmVolume::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    border: 3px solid #e4d7c7;
    height: 20px;
    width: 12px;
    border-radius: 0px;
    background: #242424;
    margin-top: -8px;
}

